'use client';

import { useState, useEffect } from 'react';
import { onSnapshot, type DocumentReference } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { FirestorePermissionError } from '@/firebase/errors';
import { errorEmitter } from '@/firebase/error-emitter';

interface UseDocOptions {
  // Add any options here if needed in the future
}

export function useDoc<T>(
  docRef: DocumentReference | null | undefined,
  options?: UseDocOptions
) {
  const { user: appUser } = useAuth(); // Use the app user which has the token
  const [data, setData] = useState<T | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  useEffect(() => {
    if (!docRef) {
      setIsLoading(false);
      setData(null);
      setError(null);
      return;
    }

    setIsLoading(true);

    const unsubscribe = onSnapshot(
      docRef,
      (snapshot) => {
        if (snapshot.exists()) {
           const docData = snapshot.data();
           Object.keys(docData).forEach(key => {
            if (docData[key]?.toDate && typeof docData[key].toDate === 'function') {
              docData[key] = docData[key].toDate();
            }
          });
          setData({ id: snapshot.id, ...docData } as unknown as T);
        } else {
          setData(null);
        }
        setIsLoading(false);
        setError(null);
      },
      (err) => {
        if (err.code === 'permission-denied') {
            const permissionError = new FirestorePermissionError(appUser, {
                path: docRef.path,
                operation: 'get'
            });
            // Emit the error to the global listener for toast notifications
            errorEmitter.emit('permission-error', permissionError);
            // Also set local error state for components that need to react to the error
            setError(permissionError);
        } else {
            console.error("useDoc Firestore Error:", err);
            setError(err);
        }
        setIsLoading(false);
      }
    );

    return () => unsubscribe();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [docRef?.path, appUser]);

  return { data, isLoading, error };
}
