
'use client';

import { useEffect } from 'react';
import { errorEmitter } from '@/firebase/error-emitter';
import { useToast } from '@/hooks/use-toast';

/**
 * A client-side component that listens for globally emitted 'permission-error' events
 * and displays them in a toast. This is a user-facing error handler.
 * It should be placed once in the root layout or a global provider.
 */
export default function FirebaseErrorListener() {
  const { toast } = useToast();

  useEffect(() => {
    const handleError = (error: any) => {
       console.error(error); // Also log the full error for dev debugging
       toast({
         variant: "destructive",
         title: "Permission Denied",
         description: "You don't have permission to perform that action.",
       });
    };

    errorEmitter.on('permission-error', handleError);

    // Cleanup listener on component unmount
    return () => {
      errorEmitter.off('permission-error', handleError);
    };
  }, [toast]);

  // This component does not render anything to the DOM.
  return null;
}
