'use client';

import React, { createContext, useContext, useState, ReactNode, useCallback, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { signOut, GoogleAuthProvider, signInWithPopup, type User } from 'firebase/auth';
import { doc, getDoc, setDoc } from 'firebase/firestore';
import { useFirebase } from '@/firebase';
import { Loader2 } from 'lucide-react';
import type { Volunteer } from '@/types';
import useFcmToken from '@/hooks/use-fcm-token'; // Import the useFcmToken hook

// This is the shape of the user object that the application logic uses.
export interface AppUser extends Volunteer {
  firebaseUser: User;
  token: any; // Add token to AppUser
}

interface AuthContextType {
  user: AppUser | null;
  isLoggedIn: boolean;
  isAdmin: boolean;
  isUserLoading: boolean;
  churchId: string | null;
  userId: string | null;
  signInWithGoogle: () => void;
  logout: () => void;
  firebaseUser: User | null;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

// A new, isolated hook to handle the complex async logic of authentication.
const useFirebaseAuth = () => {
  const { firestore, auth, user: fbUser, isUserLoading: isAuthServiceLoading } = useFirebase();
  const { token: fcmToken } = useFcmToken(); // Get the FCM token
  const [appUser, setAppUser] = useState<AppUser | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const processAuthChange = async () => {
      if (isAuthServiceLoading) {
        setIsLoading(true);
        return;
      }

      if (!fbUser) {
        setAppUser(null);
        setIsLoading(false);
        return;
      }

      setIsLoading(true);
      try {
        const userDocRef = doc(firestore, 'users', fbUser.uid);
        const userDocSnap = await getDoc(userDocRef);
        let userProfile: Volunteer;
        
        // If the user's profile doesn't exist in Firestore, they need to go through role selection.
        if (!userDocSnap.exists()) {
          setAppUser(null); // Explicitly set to null to trigger redirection to role selection
          setIsLoading(false);
          return;
        }

        userProfile = userDocSnap.data() as Volunteer;

        // If there's a new FCM token, update it in Firestore.
        if (fcmToken && userProfile.fcmToken !== fcmToken) {
          await setDoc(userDocRef, { fcmToken: fcmToken }, { merge: true });
          userProfile.fcmToken = fcmToken; // Update the local profile object as well
        }

        const tokenResult = await fbUser.getIdTokenResult();
        
        // Check if the custom claims on the token match what's in the user's Firestore document.
        const claimsNeedUpdate = !tokenResult.claims.admin || tokenResult.claims.churchId !== userProfile.churchId;
        
        let finalTokenResult = tokenResult;

        if (claimsNeedUpdate) {
            // If claims are missing or incorrect, call the API to set them.
            await fetch('/api/set-claims', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ uid: fbUser.uid, role: userProfile.role, churchId: userProfile.churchId }),
            });
            // CRITICAL: Force a refresh of the token to get the new claims immediately.
            finalTokenResult = await fbUser.getIdTokenResult(true); 
        }

        // Now that claims are guaranteed to be fresh, create the app user object.
        const enrichedUser: AppUser = { 
          ...userProfile, 
          id: fbUser.uid, 
          firebaseUser: fbUser,
          token: finalTokenResult.claims,
        };
        setAppUser(enrichedUser);
        
      } catch (error) {
        console.error("Auth Profile/Claims Error:", error);
        await signOut(auth); // Sign out on error to prevent being in a broken state
        setAppUser(null);
      } finally {
        setIsLoading(false); // Only set loading to false after all async operations are complete.
      }
    };
    processAuthChange();
  }, [fbUser, isAuthServiceLoading, firestore, auth, fcmToken]); // Add fcmToken to the dependency array
  
  return { appUser, firebaseUser: fbUser, isUserLoading: isLoading };
}


export function AuthProvider({ children }: { children: ReactNode }) {
  const { auth } = useFirebase();
  const router = useRouter();
  const { appUser, firebaseUser, isUserLoading } = useFirebaseAuth();

  const signInWithGoogle = useCallback(async () => {
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
    } catch (error: any) {
      if (error.code !== 'auth/popup-closed-by-user' && error.code !== 'auth/cancelled-popup-request') {
        console.error("Google Sign-In Error:", error);
      }
    }
  }, [auth]);

  const logout = useCallback(async () => {
    await signOut(auth);
    router.push('/');
  }, [auth, router]);
  
  // This effect handles redirection based on the final user state.
  useEffect(() => {
    if (!isUserLoading) {
      if (firebaseUser) {
        if (appUser) {
            // User is logged in and has a profile with claims.
            // Safe to proceed with role-based routing.
            const currentPath = window.location.pathname;
            if (appUser.role === 'admin') {
                if (currentPath.startsWith('/my-') || currentPath === '/role-selection') {
                    router.push('/admin');
                }
            } else { // 'volunteer'
                 if (currentPath.startsWith('/admin') || currentPath === '/role-selection') {
                    router.push('/my-schedule');
                }
            }
        } else {
          // User is logged in (has firebaseUser) but has no app profile (appUser is null).
          // They must select a role.
          const currentPath = window.location.pathname;
          // Allow them to be on role-selection or signup pages.
          const isAllowedPath = currentPath.startsWith('/role-selection') || currentPath.startsWith('/signup');
          if(!isAllowedPath) {
            router.push('/role-selection');
          }
        }
      } else {
         // User is not logged in at all.
         const currentPath = window.location.pathname;
         const isPublicPage = currentPath === '/' || currentPath.startsWith('/signup') || currentPath.startsWith('/share');
         if (!isPublicPage) {
            router.push('/');
         }
      }
    }
  }, [isUserLoading, firebaseUser, appUser, router]);


  if (isUserLoading) {
      return (
        <div className="flex min-h-screen flex-col items-center justify-center gap-4">
          <Loader2 className="h-16 w-16 text-primary animate-spin" />
          <p className="text-muted-foreground">Authenticating...</p>
        </div>
      );
  }

  const value = {
    user: appUser,
    isLoggedIn: !!appUser,
    isAdmin: appUser?.role === 'admin',
    isUserLoading: isUserLoading,
    churchId: appUser?.churchId || null,
    userId: appUser?.id || null,
    signInWithGoogle,
    logout,
    firebaseUser,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
