
'use client';
import type { AppUser } from "@/hooks/use-auth";

export type SecurityRuleContext = {
  path: string;
  operation: 'get' | 'list' | 'create' | 'update' | 'delete' | 'write';
  requestResourceData?: any;
};

export interface FirestorePermissionErrorRequest {
    auth: {
        uid: string;
        token: any;
    } | null;
    method: SecurityRuleContext['operation'];
    path: string;
    resource?: any;
}

const buildErrorMessage = (request: FirestorePermissionErrorRequest) => {
  // A helper to remove circular references for JSON.stringify
  const getCircularReplacer = () => {
    const seen = new WeakSet();
    return (key: any, value: any) => {
      if (typeof value === "object" && value !== null) {
        if (seen.has(value)) {
          return;
        }
        seen.add(value);
      }
      return value;
    };
  };
  
  return `FirestoreError: Missing or insufficient permissions: The following request was denied by Firestore Security Rules:
${JSON.stringify(request, getCircularReplacer(), 2)}`;
};

const buildRequestObject = (user: AppUser | null, context: SecurityRuleContext): FirestorePermissionErrorRequest => {
  return {
    auth: user ? {
        uid: user.id,
        token: user.token,
    } : null,
    method: context.operation,
    path: `/databases/(default)/documents/${context.path}`,
    ...(context.requestResourceData && { resource: { data: context.requestResourceData } }),
  };
}

export class FirestorePermissionError extends Error {
  public request: FirestorePermissionErrorRequest;

  constructor(user: AppUser | null, context: SecurityRuleContext) {
    const requestObject = buildRequestObject(user, context);
    const message = buildErrorMessage(requestObject);
    super(message);
    this.name = 'FirestorePermissionError';
    this.request = requestObject;
  }
}
